var slovarg={' ':' ','a':'ùñÜ','b':'ùñá','c':'ùñà','d':'ùñâ','e':'ùñä','f':'ùñã','g':'ùñå','h':'ùñç','i':'ùñé','j':'ùñè','k':'“ù','l':'ùñë','m':'ùñí',
    'n':'ùñì','o':'ùñî','p':'ùñï','q':'ùññ','r':'ùñó','s':'ùñò','t':'ùñô','u':'ùñö','v':'ùñõ','w':'ùñú','x':'ùñù','y':'ùñû','z':'ùñü','1':'Ôºë','2':'Ôºí','3':'Ôºì',
    '4':'Ôºî','5':'Ôºï','6':'Ôºñ','7':'Ôºó','8':'Ôºò','Ôºô':'‚Ö®','0':'Ôºê','A':'ùï¨','B':'ùï≠','C':'ùïÆ','D':'ùïØ','E':'ùï∞','F':'ùï±','G':'ùï≤','H':'ùï≥','I':'ùï¥',
    'J':'ùïµ','K':'ùï∂','L':'ùï∑', 'M':'ùï∏','N':'ùïπ','O':'ùï∫','P':'ùïª','Q':'ùïº','R':'ùïΩ','S':'ùïæ','T':'ùïø','U':'ùñÄ','V':'ùñÅ','W':'ùñÇ','X':'ùñÉ','Y':'ùñÑ','Z':'ùñÖ'};
function Transpo(props) {
    const text=props.text;
    const mode=props.mode;
    const texta=text.split('');
    if(mode==='g') {
        for (let i = 0; i < texta.length; i++) {
            if (texta[i] in slovarg) {
                texta[i] = slovarg[texta[i]];
            }
        }
        return <h1>{texta.join('')}</h1>;
    }else{
        return <h1>{texta.join('')}</h1>;
    }
}
export default Transpo;